<template>
    <!-- 第一個筆記 -->
    <div class="rounded text-left notecustom ">
      <h3 class="mb-4">重頭創建Vue Cli的流程</h3>
      <p>首先要建立新的專案資料夾，在想要的目錄下輸入該指令: <strong>npm init webpack projectname</strong>，後面的projectname可以自行輸入想要的專案名稱。<br>接著就會開始設定專案基本內容，這邊就依據個人需求來做設定即可。</p>
      <p>如果想要載入bootstrap的套件，可以輸入該指令: <strong>npm install bootstap node-sass sass-loader --save</strong>，因為Vue Cli並沒有Sass的相關套件，所以指令多輸入了 <strong>node-sass、sass-loader</strong> 這兩個指令，來把相關的套件安裝進來。</p>
      <p>接著就可以到App.vue的檔案裡面，在style的地方加上lang="scss"，然後加上@import "~bootstrap/scss/bootstrap"這段程式碼，如此一來就可以將bootstrap的功能給引進來了，如果有出錯的情況發生，可能是因為最新版本的問題，這時候建議安裝舊版本，輸入該指令來安裝舊版本: <strong>npm install sass-loader@7.1.0 --save -d</strong>，應該就可以正確載入bootstrap功能了。</p>
      <p>如果想要使用jquery的功能的話，輸入該指令將相關套件給載入: <strong>npm install --save jquery popper.js</strong>，因為Bootstrap會需要仰賴jquery及popper.js才能正常運行。 </p>
    </div>
</template>

<script>
export default {
  name: 'NoteTow',
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.notecustom{
  color: black;
  border: 2px solid rgba(173, 234, 234, 0.8);
  background-color: rgba(173, 234, 234, 0.8);
}
</style>



