<template>
  <div>
    <!-- 第三個筆記 -->
    <div class="rounded text-left notecustom ">
      <h3 class="mb-4">用CSS將單行文字置中</h3>
      <p>只要使用line-height的屬性就可以達成想要的效果。<br>
        如果該區塊的高度為200px，那麼就將line-height的屬性也調整為200px就行。<br>
        例如底下的結果。
      </p>
      <div class="textarea">
        <p>這邊的文字是單文文字，使用CSS中line-height的屬性讓文字被垂直置中。</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NoteOne',
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.notecustom{
  color: black;
  border: 2px solid rgba(173, 234, 234, 0.8);
  background-color: rgba(173, 234, 234, 0.8);
}
.textarea{
  height: 200px;
  line-height: 200px;
  background-color: rgba(250, 200, 200, 0.8);
}
</style>



