<template>
  <div>
    <!-- 第二個筆記 -->
    <div class="rounded text-left notecustom ">
      <h3 class="mb-4">建立視差滾動的方式</h3>
      <p>首先固定住最底層的背景圖片，利用background-attachment: fixed 的方式來達成。</p>
      <p>我們先製作點選a連結時能順暢的移動到指定的位置的效果，這部分要在JS內來完成，這邊我們使用到的是jQeury的套件，首先選取要被點選的a連結，然後使用click的方法，用this選取a連結本身，在使用attr()來讀取裡面的屬性href，使用變數將取得的值儲存下來。</p>
      <p>使用變數targetPos來紀錄取得剛剛讀出的href的位置所在，要取得該id的位置要使用 <strong>.offset().top</strong>的方式。最後使用<strong>scrollTop: targetPos</strong>來移動到指定的id位置，最後再加上<strong>preventDefault</strong>來避免a連結被點擊時會先到指定錨點的情況發生。</p>
      <p>再來製作出滾動時讀取出滾動位置的方法。使用<strong>$(window).scroll(function(){})</strong>，可以在視窗滾動時觸發事件效果。</p>
      <nav class="nav nav-pills nav-fill sticky-top">
        <a class="scrollLink nav-link" href="#introduction">個人簡介</a>
        <a class="scrollLink nav-link" href="#skills">技能掌握</a>
        <a class="scrollLink nav-link" href="#works">作品連結</a>
      </nav>
      <div id="introduction">
        <h4 class="my-3 text-center" >個人簡介</h4>
        <div class="row myself">
          <div class="col">
            <p class="p-3 pt-4">
              你好，我是Ethan，對於程式設計有興趣，想要成為程式工程師，所以目前正在努力學習相關的技術，希望能夠進入這個領域發展!</p>
          </div>
          <div class="col">
            <img class="img-fluid rounded p-2" src="../../assets/貓咪.jpg" alt="">
          </div>
        </div>
      </div>
      <div id="skills">
        <h4 class="my-3 text-center ">技能掌握</h4>
        <p class="p-3">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Voluptatibus eveniet omnis praesentium tempore laboriosam, hic blanditiis iusto recusandae consequuntur dicta eum veritatis labore sint minima sit sequi sunt beatae, exercitationem quo? Quae optio praesentium animi aut ratione eaque a soluta iure, maxime, veritatis, asperiores atque delectus alias? Eveniet, iusto. Atque?</p>
      </div>
      <div id="works">
        <h4 class="my-3 text-center" >作品連結</h4>
        <p class="p-3">底下是我放在Github上的作品連結</p>
        <ul>
          <li>
            <a href="https://ethan50422.github.io/LearningEnglish/">作品一: LearningEnglish</a></li>
          <li>
            <a href="https://ethan50422.github.io/EthanNote/#/">作品二: EthanBlog</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'NoteOne',
  data () {
    return {
    }
  }
}

$(document).ready(function () {
  // a連結被點擊後移動到指定地點
  $('.scrollLink').click(function (e) {
    e.preventDefault()
    const target = $(this).attr('href')
    const targetPos = $(target).offset().top
    // 滑動到指定id地點的jquery方式
    $('html, body').animate({
      scrollTop: targetPos
    }, 800)
  })
  // 滾動效果
  $(window).scroll(function () {
    // 這是可以取得距離瀏覽器上方有多遠的方法
    const scrollPos = $(window).scrollTop()
    // console.log(scrollPos)
    $('.scrollLink').each(function () {
      const target = $(this).attr('href')
      const targetPos = $(target).offset().top
      const targetHeight = $(target).outerHeight()
      // console.log(target, targetPos, targetHeight)
      if (targetPos - 50 <= scrollPos && (targetPos + targetHeight) > scrollPos) {
        $('.scrollLink').removeClass('active')
        $(this).addClass('active')
      } else {
        $(this).removeClass('active')
      }
    })
  })
})
</script>

<style scoped>
.notecustom{
  color: black;
  border: 2px solid rgba(173, 234, 234, 0.8);
  background-color: rgba(173, 234, 234, 0.8);
}
.myself{
  background-color: white;
}
</style>



