<template>
    <div>
        <button class="btn btn-primary" @click="addcompcount">click me!</button>
    </div>
</template>

<script>
export default {
  data () {
    return {
      outercount: 1
    }
  },
  props: ['compcount'],
  methods: {
    addcompcount () {
    //   this.compcount = this.compcount + 1
      this.$emit('incresecompcount', Number(this.outercount))
    }
  }
}
</script>



