import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import MyNote from '@/components/MyNote'
import MyRecord from '@/components/MyRecord'
import NoteOne from '@/components/pages/NoteOne'
import NoteTwo from '@/components/pages/NoteTwo'
import NoteThree from '@/components/pages/NoteThree'
import NoteFour from '@/components/pages/NoteFour'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/myrecord',
      name: 'MyRecord',
      component: MyRecord
    },
    {
      path: '/mynote',
      name: 'MyNote',
      component: MyNote,
      children: [
        {
          path: 'noteone',
          name: 'noteone',
          component: NoteOne
        },
        {
          path: 'notetwo',
          name: 'notetwo',
          component: NoteTwo
        },
        {
          path: 'notethree',
          name: 'notethree',
          component: NoteThree
        },
        {
          path: 'notefour',
          name: 'notefour',
          component: NoteFour
        }
      ]
    }

  ]
})



