<template>
  <div>
    <!-- 第四個筆記 -->
    <div class="rounded text-left notecustom ">
      <h3 class="mb-4">Vue元件的使用</h3>
      <p>如果要將其他JS的檔案引進來，需要在HTML結構下的scrip中加入屬性type="module"，才能正確引進被export的JS檔案。<br>
        如果沒有設定type的話，默認情況下是type="text/javascript"。而被輸出的js檔案使用export default name的方式做輸出就好，注意一個檔案只能有一個export default輸出。</p>
      <p>Vue的元件製作: 製作一個元件按鈕，點擊以後可以將外圍的資料數量+1，所以外圍要先建立method可以將數量+1</p>
      <Button :compcount="count" @incresecompcount="addcount"></Button>
    </div>
  </div>
</template>

<script>
import Button from '@/components/button'

export default {
  name: 'NoteOne',
  data () {
    return {
      count: 0
    }
  },
  methods: {
    addcount (newCount) {
      const vm = this
      vm.count = vm.count + newCount
    }
  },
  components: {
    Button
  }
}
</script>

<style scoped>
.notecustom{
  color: black;
  border: 2px solid rgba(173, 234, 234, 0.8);
  background-color: rgba(173, 234, 234, 0.8);
}
</style>



